import { Component, Pipe } from "@angular/core";
import { NavController, AlertController } from "ionic-angular";
import { FormPage } from "../form/form";
import { DataProvider } from "../../providers/data/data";
import { Task } from "../../models/task";
import { HttpClient } from "@angular/common/http";
import { NativeAudio } from "@ionic-native/native-audio";
import { AudioProvider } from "../../providers/audio/audio";

@Component({
  selector: "page-home",
  templateUrl: "home.html"
})
@Pipe({ name: "keys" })
export class HomePage {
  //tasks:any[];
  tasks: Task[];
  baseuri = "http://localhost:3200/api/todos";

  // the 'it-IT' helps display time in 24 hrs
  //taskStartTime = new Date().toLocaleTimeString("it-IT");
  taskStartTime;
  constructor(
    public navCtrl: NavController,
    public dataProvider: DataProvider,
    public http: HttpClient,
    public alertCtrl: AlertController,
    public nativeAudio: NativeAudio,
    public audioProvider: AudioProvider
  ) {
    this.getTasks();
    this.taskStatusAlert();
    this.audioProvider.preload("alertSound", "assets/audio/boom.wav");
    // update seconds in real-time
    setInterval(() => {
      this.taskStartTime = new Date().toLocaleTimeString("it-IT");
    }, 1000);
    this.taskStatusAlert();
  }

  gotoAddTaskPage() {
    this.navCtrl.push(FormPage);
  }

  deleteTask(task) {
    let alert = this.alertCtrl.create({
      title: "Confirm delete",
      message: "Do you want to delete this task?",
      buttons: [
        {
          text: "Delete",
          role: "destructive",
          handler: () => {
            this.dataProvider.deleteTodo(task._id).subscribe(task => {
              console.log(task, "DELETED!");
            });
          }
        },
        {
          text: "Cancel",
          role: "cancel",
          handler: () => {
            console.log("Cancel clicked");
          }
        }
      ]
    });
    alert.present();
  }

  getTasks() {
    this.dataProvider.getTodos().subscribe(data => {
      this.tasks = data;
      console.log("TASKS---", this.tasks);
    });
  }

  playAudio() {
    this.audioProvider.play("alertSound");
  }

  taskStatusAlert() {
    //setTimeout(() => {
    setInterval(() => {
      let status = this.tasks.find(
        task => task.isComplete == false && task.taskTime == this.taskStartTime
      );
      //console.log("TASK START TIME: ", this.taskStartTime);
      if (status) return this.audioProvider.play("alertSound");
    }, 1000);
    // }, 2000);
  }

  editTodo(task) {
    let alert = this.alertCtrl.create({});
    // alert.present();
  }

  toggleStatus(task) {
    this.dataProvider.toggleStatus(task._id, task).subscribe(data => {
      console.log(data);
    });
  }
}



